import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { NzMessageService } from 'ng-zorro-antd';
import { CookieService } from 'ngx-cookie-service';
import { AbstractControl, FormBuilder, FormGroup, Validators, ReactiveFormsModule } from '@angular/forms';
import { MySharedService } from '../my-shared.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  displayText = 'show-class';
  imgurl = "api/AuthCode/Creat" + "?r=" + Math.random();

  visible = true;
  myDate: any;
  isOkLoading = false;
  option = {
    Account: "",
    Password: "",
    Code: ""
  };
  constructor(
    public router: Router,
    private http: HttpClient,
    private message: NzMessageService,
    private cookieService: CookieService,
    private fb: FormBuilder,
    private mySharedService: MySharedService
  ) { }

  ChangeCode() {
    this.imgurl = "api/AuthCode/Creat" + "?r=" + Math.random();
  }
  ngOnInit() {
    this.validateForm = this.fb.group({
      userName: [null, [Validators.required]],
      password: [null, [Validators.required]],
      code: [null, [Validators.required]],
      remember: [true]
    });
  }

  toggle() {
    this.visible = !this.visible;
    this.displayText = this.visible ? 'show-class' : 'hide-class';
  }
  validateForm: FormGroup;

  submitForm() {
    for (const i in this.validateForm.controls) {
      this.validateForm.controls[ i ].markAsDirty();
      this.validateForm.controls[ i ].updateValueAndValidity();
    }
    if(this.validateForm.valid == true){
      //做登录
      this.option.Account =this.validateForm.controls.userName.value;
      this.option.Password = this.validateForm.controls.password.value;
      this.option.Code = this.validateForm.controls.code.value;
      this.SysUserLogin();
    }

  }
  SysUserLogin() {
    let url = "api/Sys_User/SysLogin";
    this.http.post(url, this.option).subscribe(
      (date) => {
        this.myDate = date;
        //设置cookie
        this.cookieService.deleteAll();
        console.log(this.myDate);

        this.cookieService.set( 'badun_sys_Account', this.myDate.account );
        this.cookieService.set( 'badun_sys_Token', this.myDate.token );
        this.cookieService.set( 'badun_sys_Id', this.myDate.id );
        this.cookieService.set( 'badun_sys_Name', this.myDate.name );
        this.cookieService.set( 'badun_sys_Logo', this.myDate.logo );
        this.cookieService.set( 'badun_sys_RoleId', this.myDate.roleId );
        this.router.navigateByUrl("workspace");
      },
      response => {
       this.message.create("error", response, { nzDuration: 3000 });
       this.ChangeCode();
      }
    )
  }
}
